import { fetchImages } from './js/pixabay-api';
import './css/loader-styles.css';
import 'simplelightbox/dist/simple-lightbox.min.css';
import {
  renderGallery,
  galleryElement,
  showEndOfCollectionMessage,
} from './js/render-functions';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const searchForm = document.querySelector('.form');
const inputElement = document.querySelector('.search-input');
const loader = document.querySelector('.loader');
const loadMoreBtn = document.querySelector('.load-more-btn');

hideLoader();
let searchTerm = '';
let pageCounter = 1;
const perPage = 15;

searchForm.addEventListener('submit', submitHandle);

async function submitHandle(event) {
  event.preventDefault();
  searchTerm = inputElement.value.trim();
  console.log(searchTerm);
  if (searchTerm === '') {
    iziToast.error({
      title: 'Error',
      message: 'Please enter a search term.',
      position: 'topCenter',
    });

    return;
  }

  showLoader();
  try {
    const images = await fetchImages(searchTerm, pageCounter, perPage);
    if (images.length === 0) {
      galleryElement.innerHTML = '';
      iziToast.info({
        title: 'Info',
        message:
          'Sorry, there are no images matching your search query. Please try again!',
        position: 'topCenter',
      });
    } else {
      renderGallery(images);
      inputElement.value = '';
      loadMoreBtn.style.display = 'flex';
    }
  } catch (error) {
    console.error('Error fetching images:', error);
    iziToast.error({
      title: 'Error',
      message: 'Failed to fetch images. Please try again later.',
      position: 'topCenter',
    });
  } finally {
    hideLoader();
  }
}

loadMoreBtn.addEventListener('click', async () => {
  try {
    showLoader();
    const images = await fetchImages(searchTerm, (pageCounter += 1), perPage);
    renderGallery(images);
    if (images.length < perPage) {
      loadMoreBtn.style.display = 'none';
      showEndOfCollectionMessage();
    }
  } catch (error) {
    console.error('Error fetching more images:', error);
    iziToast.error({
      title: 'Error',
      message: `Error fetching more images: ${error}`,
    });
  } finally {
    hideLoader();
    smoothScrollToGallery();
  }
});

// * scroll
function smoothScrollToGallery() {
  const galleryHeight = galleryElement.getBoundingClientRect().height;
  window.scrollBy({ top: -document.body.scrollHeight, behavior: 'smooth' });
}
// *loader
function showLoader() {
  loader.classList.remove('hidden');
}

function hideLoader() {
  loader.classList.add('hidden');
}
